class Collapsible extends Component {
  render() {
    const { label, children, isOpen, id, handleClick } = this.props;
    return (
      <div>
        <h2 onClick={handleClick.bind(null, id)}>{label}</h2>
        {isOpen ? <div>{children}</div> : null}
      </div>
    );
  }
}

class RookDetails extends React.Component {
  state = {
    openedId: 1
  };

  handleClick = openedId => {
    this.setState({ openedId });
  };
  render() {
    const { openedId } = this.state;
    return (
      <div className="collapse_details">
        {rook_details.map((section) =>
          <div className="collapse_details_label collapse_details_label_rook">
            <Collapsible
              label={section.label}
              id={section.id}
              isOpen={openedId === section.id}
              handleClick={this.handleClick}
              active="activeSection"
            >
              {section.description && <p className="collapse_details_description collapse_details_description_rook">{section.description}</p>}
              {section.id === 3
                &&
                <div className="collapse_details_description collapse_details_description_rook">
                  {section.lines.map((s) =>
                    <div>
                      <p className="collapse_details_description_dialogue_label">{"ABOUT " + s.about.toUpperCase()}</p>
                      <p className="collapse_details_description_dialogue">{s.line}</p>
                    </div>)}
                </div>
              }
              {section.id === 4
                &&
                <div className="collapse_details_description collapse_details_description_rook">
                  {section.card_deck.map((c) =>
                    <div>
                      <p className="collapse_details_description_dialogue_label">{c.spell}</p>
                      <p className="collapse_details_description_dialogue">{c.description}</p>
                    </div>)}
                </div>
              }
            </Collapsible>
          </div>
        )}
      </div>
    );
  }
}

export default RookDetails;
